import { Info } from '@mui/icons-material';
import { Box, Button, LinearProgress, Modal, Typography } from '@mui/material';
import React from 'react'
import LinearWithValueLabel from './LinearWithValueLabel';

const DepositModal = () => {

  const defaultModal = {
    position: "absolute",
    top: "50%",
    left: "50%",
    transform: "translate(-50%, -50%)",
    width: 400,
    bgcolor: "background.paper",
    boxShadow: 24,
    borderRadius: "8px",
    p: 4,
  }

    const [verifyModal, verifyModalActive] = React.useState(false);

    const openVerifyModal = () => {
      verifyModalActive(true);
    };
    const closeVerifyModal = () => {
      verifyModalActive(false);
    };

  return (

    <div>
<Modal
        open={verifyModal}
        onClose={closeVerifyModal}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box sx={defaultModal}>
          <Typography id="modal-modal-title" variant="h6" component="h2">
            Verify account
          </Typography>
          {/* <Typography id="modal-modal-description" sx={{ mt: 2, marginBottom: '1rem'}}>
          Thank you for creating your account with us, to verify and authorize your account, please deposit a minimum of $50 worth of assets into your account. After which, your account will be officially verified, and you will be able to enjoy all the benefits and features of our platform.
          </Typography>
          <Typography id="modal-modal-description" sx={{ mt: 2, marginBottom: '1rem'}}>
          For account verification purposes, we require a minimum deposit of $50 worth of assets. Once this requirement is met, your account will be promptly verified.
          </Typography>
          <Typography id="modal-modal-description" sx={{ mt: 2, marginBottom: '1rem'}}>
          To confirm the validity of your account, please deposit a minimum of $50 worth of assets. Once this process is complete, your account verification will be automatically processed.
          </Typography> */}
          <Typography id="modal-modal-description" sx={{ mt: 2, marginBottom: '1rem'}}>
          Your account balance is currently insufficient for account verification. Once your balance reaches $50, your verification status will be updated accordingly. Please make any additional deposits or purchase assets to meet your minimum account verification deposit limit and enable access to all platform benefits and features.
          </Typography>

          <Box>
            <Typography variant='subtitle1' sx={{marginBottom: '0px', paddingBottom: '0px'}}>
            Verification Status: 
            </Typography>
            <Box>

            <LinearWithValueLabel/></Box>
            </Box>
            <Typography variant='subtitle2' sx={{marginBottom: '0px', paddingBottom: '0px'}}>
            Once the minimum required deposit of $50 is met, your account will be authorized and your verification status will be updated accordingly.
            </Typography>
        </Box>
      </Modal>
    <Button
                  sx={{ marginLeft: "1rem" }}
                  startIcon={<Info />}
                  variant={"contained"}
                  color="primary"
                  onClick={openVerifyModal}
                  >
                  Get Verified
                </Button>
                    </div>

  )
}

export default DepositModal;