import * as React from "react";
import PropTypes from "prop-types";
import AppBar from "@mui/material/AppBar";
import Avatar from "@mui/material/Avatar";
import Button from "@mui/material/Button";
import Grid from "@mui/material/Grid";
import HelpIcon from "@mui/icons-material/Help";
import IconButton from "@mui/material/IconButton";
import Link from "@mui/material/Link";
import MenuIcon from "@mui/icons-material/Menu";
import NotificationsIcon from "@mui/icons-material/Notifications";
import Tab from "@mui/material/Tab";
import Tabs from "@mui/material/Tabs";
import Toolbar from "@mui/material/Toolbar";
import Tooltip from "@mui/material/Tooltip";
import Typography from "@mui/material/Typography";
import {
  Alert,
  Box,
  Chip,
  Divider,
  FormControl,
  ListItemIcon,
  ListItemText,
  Menu,
  MenuItem,
  MenuList,
  Modal,
  Paper,
  Snackbar,
  Stack,
  TextField,
  TextareaAutosize,
} from "@mui/material";
import { Check, Info, ContentCut, ContentPaste } from "@mui/icons-material";
import LogoutIcon from "@mui/icons-material/Logout";
import DepositModal from "./VerifyModal";
const lightColor = "rgba(255, 255, 255, 0.7)";

function Header(props) {
  const { onDrawerToggle } = props;

  let username = "";
  if (localStorage.getItem("auth")) {
    username = JSON.parse(localStorage.getItem("auth")).username;
  } else {
    username = JSON.parse(localStorage.getItem("customAuth")).username;
  }

  const [anchorEl, setAnchorEl] = React.useState(null);
  const open = Boolean(anchorEl);
  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };
  const handleLogout = () => {
    localStorage.removeItem("auth");
    localStorage.setItem("logout", true);
    window.location.reload();
  };

  const [bugModal, bugModalActive] = React.useState(false);
  const defaultModal = {
    position: "absolute",
    top: "50%",
    left: "50%",
    transform: "translate(-50%, -50%)",
    width: 400,
    bgcolor: "background.paper",
    boxShadow: 24,
    borderRadius: "8px",
    p: 4,
  };

  const openBugModal = () => {
    bugModalActive(true);
  };
  const closeBugModal = () => {
    bugModalActive(false);
  };

  const sendBugReport = (e) => {
    e.preventDefault();
    setLoading(true);
    setTimeout(() => {
        closeBugModal();
      setLoading(false);
      handleClickToast();
    }, Math.floor(Math.random() * 1000 + 250));
  };

  const [loading, setLoading] = React.useState();

  const textAreaStyle = {
    margin: "1rem 0",
    width: "100%",
    maxHeight: "30vh",
    fontFamily: "IBM Plex Sans, sans-serif",
    fontSize: "0.875rem",
    fontWeight: "400",
    lineHeight: "1.5",
    padding: "12px",
    borderRadius: "12px 12px 0 12px",
  };

  const [openToast, setOpenToast] = React.useState(false);

  const handleClickToast = () => {
    setOpenToast(true);
  };

  const handleCloseToast = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }

    setOpenToast(false);
  };

  return (
    <React.Fragment>
      <Stack spacing={2} sx={{ width: "100%" }}>
        <Snackbar
          open={openToast}
          autoHideDuration={6000}
          onClose={handleCloseToast}
        >
          <Alert
            onClose={handleCloseToast}
            severity="info"
          >
            Thank you for reporting the issue you encountered. The bug you<br/>
            brought to our attention is highly valuable to our development team<br/>
            to ensure that our users experience a seamless and enjoyable<br/>
            application.<br/>
            <br />
            We apologize for the inconvenience this bug caused, and our team is<br/>
            working swiftly to resolve it. Rest assured, we strive to provide<br/>
            the best user experience and appreciate your help in accomplishing<br/>
            this goal.
          </Alert>
        </Snackbar>
      </Stack>
      <Modal
        open={bugModal}
        onClose={closeBugModal}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box sx={defaultModal}>
          <Typography id="modal-modal-title" variant="h6" component="h2">
            Report a bug
          </Typography>
          <Typography id="modal-modal-description" sx={{ mt: 2 }}>
            <form autoComplete="off" onSubmit={sendBugReport}>
              <FormControl sx={{ width: "100%" }}>
                <Typography>
                  Please provide a thorough description of the bug you
                  encountered and the steps you took to reproduce it. This
                  information is essential for replicating the issue and
                  resolving it efficiently.
                </Typography>
                <TextareaAutosize
                  required
                  placeholder={"Please enter the description here."}
                  style={textAreaStyle}
                />

                <Button variant="contained" type="submit" disabled={loading}>
                  Submit
                </Button>
              </FormControl>
            </form>
          </Typography>
        </Box>
      </Modal>

      <AppBar color="primary" position="sticky" elevation={0}>
        <Toolbar>
          <Grid container spacing={1} alignItems="center">
            <Grid sx={{ display: { sm: "none", xs: "block" } }} item>
              <IconButton
                color="inherit"
                aria-label="open drawer"
                onClick={onDrawerToggle}
                edge="start"
              >
                <MenuIcon />
              </IconButton>
            </Grid>
            <Grid item xs />

            <Grid item>
              <Tooltip title="Alerts  No alerts">
                <IconButton color="inherit">
                  <NotificationsIcon />
                </IconButton>
              </Tooltip>
            </Grid>
            <Grid item>
              <IconButton
                color="inherit"
                sx={{ p: 0.5 }}
                id="basic-button"
                aria-controls={open ? "basic-menu" : undefined}
                aria-haspopup="true"
                aria-expanded={open ? "true" : undefined}
                onClick={handleClick}
              >
                <Avatar alt={username} />
              </IconButton>
            </Grid>

            <div>
              <Menu
                id="basic-menu"
                anchorEl={anchorEl}
                open={open}
                onClose={handleClose}
                MenuListProps={{
                  "aria-labelledby": "basic-button",
                }}
              >
                <Typography variant="subtitle2" sx={{textAlign: 'center'}}>Welcome, <strong>{localStorage.getItem('auth') ? (JSON.parse(localStorage.getItem('auth')).username) : (JSON.parse(localStorage.getItem('customAuth')).username)}</strong></Typography>
                {/* <MenuItem onClick={handleClose}>Profile</MenuItem>
        <MenuItem onClick={handleClose}>My account</MenuItem> */}
                <MenuItem>
                  {localStorage.getItem("auth") ? (
                    <Chip
                      sx={{ width: "100%" }}
                      icon={<Check />}
                      label="Verified"
                      color="success"
                      variant="outlined"
                    />
                  ) : (
                    <DepositModal />
                  )}
                </MenuItem>
                <Divider />
                <MenuItem onClick={handleLogout}>
                  <ListItemIcon>
                    <LogoutIcon fontSize="small" />
                  </ListItemIcon>
                  <ListItemText>Sign out</ListItemText>
                </MenuItem>
              </Menu>
            </div>
          </Grid>
        </Toolbar>
      </AppBar>
      <AppBar
        component="div"
        color="primary"
        position="static"
        elevation={0}
        sx={{ zIndex: 0 }}
      >
        <Toolbar>
          <Grid container alignItems="center" spacing={1}>
            <Grid item xs>
              <Typography color="inherit" variant="h5" component="h1">
                Overview
              </Typography>
            </Grid>
            <Grid item>
              <Button
                sx={{ borderColor: lightColor }}
                variant="outlined"
                color="inherit"
                size="small"
                onClick={openBugModal}
              >
                Report a bug
              </Button>
            </Grid>
            <Grid item>
              {/* <Tooltip title="Help"> */}
              <IconButton color="inherit">
                <HelpIcon />
              </IconButton>
              {/* </Tooltip> */}
            </Grid>
          </Grid>
        </Toolbar>
      </AppBar>
      <AppBar
        component="div"
        position="static"
        elevation={0}
        sx={{ zIndex: 0 }}
      >
        <Tabs value={0} textColor="inherit">
          <Tab label="Assets" />
          <Tab label="Swap" onClick={()=>alert('Asset swaps are currently under maintenance. Please try again later')}/>
        </Tabs>
      </AppBar>
    </React.Fragment>
  );
}

Header.propTypes = {
  onDrawerToggle: PropTypes.func.isRequired,
};

export default Header;
