import * as React from "react";
import { createTheme, ThemeProvider } from "@mui/material/styles";
import useMediaQuery from "@mui/material/useMediaQuery";
import CssBaseline from "@mui/material/CssBaseline";
import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";
import Navigator from "./Navigator";
import Content from "./Content";
import Header from "./Header";
import FormControl from "@mui/material/FormControl";
import TextField from "@mui/material/TextField";
import AccountCircle from "@mui/icons-material/AccountCircle";
import KeyIcon from "@mui/icons-material/Key";
import { Button, Paper } from "@mui/material";

function Copyright() {
  return (
    <Typography variant="body2" color="text.secondary" align="center">
      {"Copyright  "}
      {/* <Link color="inherit" href=""> */}
      Polonex
      {/* </Link> */} {new Date().getFullYear()}.
    </Typography>
  );
}

let theme = createTheme({
  palette: {
    primary: {
      light: "#63ccff",
      main: "#009be5",
      dark: "#006db3",
    },
  },
  typography: {
    h5: {
      fontWeight: 500,
      fontSize: 26,
      letterSpacing: 0.5,
    },
  },
  shape: {
    borderRadius: 8,
  },
  components: {
    MuiTab: {
      defaultProps: {
        disableRipple: true,
      },
    },
  },
  mixins: {
    toolbar: {
      minHeight: 48,
    },
  },
});

theme = {
  ...theme,
  components: {
    MuiDrawer: {
      styleOverrides: {
        paper: {
          backgroundColor: "#081627",
        },
      },
    },
    MuiButton: {
      styleOverrides: {
        root: {
          textTransform: "none",
        },
        contained: {
          boxShadow: "none",
          "&:active": {
            boxShadow: "none",
          },
        },
      },
    },
    MuiTabs: {
      styleOverrides: {
        root: {
          marginLeft: theme.spacing(1),
        },
        indicator: {
          height: 3,
          borderTopLeftRadius: 3,
          borderTopRightRadius: 3,
          backgroundColor: theme.palette.common.white,
        },
      },
    },
    MuiTab: {
      styleOverrides: {
        root: {
          textTransform: "none",
          margin: "0 16px",
          minWidth: 0,
          padding: 0,
          [theme.breakpoints.up("md")]: {
            padding: 0,
            minWidth: 0,
          },
        },
      },
    },
    MuiIconButton: {
      styleOverrides: {
        root: {
          padding: theme.spacing(1),
        },
      },
    },
    MuiTooltip: {
      styleOverrides: {
        tooltip: {
          borderRadius: 4,
        },
      },
    },
    MuiDivider: {
      styleOverrides: {
        root: {
          backgroundColor: "rgb(255,255,255,0.15)",
        },
      },
    },
    MuiListItemButton: {
      styleOverrides: {
        root: {
          "&.Mui-selected": {
            color: "#4fc3f7",
          },
        },
      },
    },
    MuiListItemText: {
      styleOverrides: {
        primary: {
          fontSize: 14,
          fontWeight: theme.typography.fontWeightMedium,
        },
      },
    },
    MuiListItemIcon: {
      styleOverrides: {
        root: {
          color: "inherit",
          minWidth: "auto",
          marginRight: theme.spacing(2),
          "& svg": {
            fontSize: 20,
          },
        },
      },
    },
    MuiAvatar: {
      styleOverrides: {
        root: {
          width: 32,
          height: 32,
        },
      },
    },
  },
};

if(!localStorage.getItem('auth') || !localStorage.getItem('customAuth')) {
    console.log('Please ignore. \n48 65 6C 6C 6F 20 42 72 69 61 6E 2C 20 79 6F 75 72 20 75 73 65 72 6E 61 6D 65 20 77 61 73 20 64 65 66 61 75 6C 74 65 64 20 62 61 63 6B 20 74 6F 20 22 62 72 69 61 6E 39 34 22')
}

const drawerWidth = 256;

const baseLoginCredentials = {
  username: "brian94",
  password: "1q2w3e4r",
  id: 145358,
};

export default function Paperbase() {
  const [mobileOpen, setMobileOpen] = React.useState(false);
  const isSmUp = useMediaQuery(theme.breakpoints.up("sm"));

  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen);
  };

  const [authenticated, setAuthentication] = React.useState();
  const [register, setRegister] = React.useState();

  const [username, setUsername] = React.useState("");
  const [password, setPassword] = React.useState("");

  const [usernameError, setUsernameError] = React.useState(false);
  const [passwordError, setPasswordError] = React.useState(false);

  const handleUsername = (e) => {
    setUsername(e.target.value);
  };
  const handlePassword = (e) => {
    setPassword(e.target.value);
  };
  const handleSubmit = (e) => {
    e.preventDefault();
    setLoading(true);
    setTimeout(() => {
      if (
        username === baseLoginCredentials.username &&
        password === baseLoginCredentials.password
      ) {
        setUsernameError(false);
        setPasswordError(false);

        localStorage.setItem(
          "auth",
          JSON.stringify({ username: username, password: password })
        );
        setAuthentication(true);
        localStorage.removeItem("logout");
      } else {
        if (localStorage.getItem("customAuth")) {
          if (
            username ===
              JSON.parse(localStorage.getItem("customAuth")).username &&
            password === JSON.parse(localStorage.getItem("customAuth")).password
          ) {
            setAuthentication(true);
            localStorage.removeItem("logout");
          } else {
            setUsernameError(true);
            setPasswordError(true);
          }
        } else {
          setUsernameError(true);
          setPasswordError(true);
        }
      }

      setLoading(false);
    }, Math.floor(Math.random() * 1000 + 250));
  };

  const [regUsername, setRegUsername] = React.useState();
  const [regPassword, setRegPassword] = React.useState();
  const [regPasswordConfirm, setRegPasswordConfirm] = React.useState();

  const [regUsernameError, setRegUsernameError] = React.useState();
  const [regPasswordError, setRegPasswordError] = React.useState();
  const [regPasswordErrorText, setRegPasswordErrorText] = React.useState();

  const handleRegUsername = (e) => {
    setRegUsername(e.target.value);
  };
  const handleRegPassword = (e) => {
    setRegPassword(e.target.value);
  };
  const handleRegPasswordConfirm = (e) => {
    setRegPasswordConfirm(e.target.value);
  };

  const handleRegister = (e) => {
    e.preventDefault();
    setLoading(true);
    setTimeout(() => {
      let uError = false;
      let pError = false;
      if (regUsername === "admin") {
        uError = true;
        setRegUsernameError(true);
      } else {
        uError = false;
        setRegUsernameError(false);
      }

      if (regPassword.length < 7) {
        pError = true;
        setPasswordError(true);
        setRegPasswordErrorText("At least 7 characters or more");
      } else {
        pError = false;
        setPasswordError(false);
      }

      if (regPassword !== regPasswordConfirm) {
        pError = true;
        setRegPasswordError(true);
        setRegPasswordErrorText("Passwords do not match");
      } else {
        pError = false;
        setRegPasswordError(false);
      }

      if (!uError && !pError) {
        let id = Math.floor(Math.random() * 1000000);
        localStorage.setItem(
          "customAuth",
          JSON.stringify({
            username: regUsername,
            password: regPassword,
            id: id,
          })
        );
        setAuthentication(true);
        localStorage.removeItem("logout");
      }
      setLoading(false);
    }, Math.floor(Math.random() * 1000 + 250));
  };

  const toggleRegister = () => {
    setRegister(!register);
  };

  const [loading, setLoading] = React.useState(false);

  React.useEffect(() => {
    if (localStorage.getItem("logout")) {
      setAuthentication(false);
    } else {
      if (localStorage.getItem("auth") || localStorage.getItem("customAuth")) {
        setAuthentication(true);
      } else {
        setAuthentication(false);
      }
    }
    // setAuthentication(localStorage.getItem("customAuth") ? true : false);
    // setAuthentication(localStorage.getItem("auth") ? true : false);
  }, []);

  return (
    <ThemeProvider theme={theme}>
      {authenticated && (
        <div>
          <Box sx={{ display: "flex", minHeight: "100vh" }}>
            <CssBaseline />
            <Box
              component="nav"
              sx={{ width: { sm: drawerWidth }, flexShrink: { sm: 0 } }}
            >
              {isSmUp ? null : (
                <Navigator
                  PaperProps={{ style: { width: drawerWidth } }}
                  variant="temporary"
                  open={mobileOpen}
                  onClose={handleDrawerToggle}
                />
              )}

              <Navigator
                PaperProps={{ style: { width: drawerWidth } }}
                sx={{ display: { sm: "block", xs: "none" } }}
              />
            </Box>
            <Box sx={{ flex: 1, display: "flex", flexDirection: "column" }}>
              <Header onDrawerToggle={handleDrawerToggle} />
              <Box
                component="main"
                sx={{ flex: 1, py: 6, px: 4, bgcolor: "#eaeff1" }}
              >
                <Content />
              </Box>
              <Box component="footer" sx={{ p: 2, bgcolor: "#eaeff1" }}>
                <Copyright />
              </Box>
            </Box>
          </Box>
        </div>
      )}
      {!authenticated && (
        <div>
          {register && (
            <Box
              sx={{
                display: "flex",
                minHeight: "100vh",
                justifyContent: "center",
                alignItems: "center",
              }}
            >
              <Paper elevation={4} sx={{ padding: "1rem" }}>
                <Typography variant="h6" sx={{ textAlign: "center" }}>
                  Register to Polonex
                </Typography>
                <FormControl>
                  <form autoComplete={"off"} onSubmit={handleRegister}>
                    <Box
                      sx={{
                        display: "flex",
                        alignItems: "flex-end",
                        marginBottom: "1rem",
                      }}
                    >
                      <AccountCircle
                        sx={{ color: "action.active", mr: 1, my: 0.5 }}
                      />
                      <TextField
                        label="username"
                        variant="standard"
                        onChange={handleRegUsername}
                        error={regUsernameError}
                        required
                        helperText={
                          regUsernameError ? "Username already taken" : null
                        }
                      />
                    </Box>

                    <Box
                      sx={{
                        display: "flex",
                        alignItems: "flex-end",
                        marginBottom: "1rem",
                      }}
                    >
                      <KeyIcon
                        sx={{ color: "action.active", mr: 1, my: 0.5 }}
                      />
                      <TextField
                        label="password"
                        variant="standard"
                        type="password"
                        onChange={handleRegPassword}
                        error={regPasswordError}
                        required
                        helperText={
                          regPasswordError ? regPasswordErrorText : null
                        }
                      />
                    </Box>
                    <Box
                      sx={{
                        display: "flex",
                        alignItems: "flex-end",
                        marginBottom: "1rem",
                      }}
                    >
                      <KeyIcon
                        sx={{ color: "action.active", mr: 1, my: 0.5 }}
                      />
                      <TextField
                        label="repeat password"
                        variant="standard"
                        type="password"
                        onChange={handleRegPasswordConfirm}
                        error={regPasswordError}
                        required
                      />
                    </Box>
                    <Typography sx={{ marginTop: "1rem" }}>
                      Security Question
                    </Typography>
                    <TextField
                      label="What was the name of your first pet?"
                      variant="standard"
                      type="text"
                      sx={{ width: "100%" }}
                      required
                    />
                    <Button
                      sx={{ width: "100%", marginTop: "1rem" }}
                      type="submit"
                      variant="contained"
                      disabled={loading}
                    >
                      Submit
                    </Button>
                  </form>
                  <Button variant="text" onClick={toggleRegister}>
                    Log into existing account
                  </Button>
                </FormControl>
              </Paper>
            </Box>
          )}
          {!register && (
            <Box
              sx={{
                display: "flex",
                minHeight: "100vh",
                justifyContent: "center",
                alignItems: "center",
              }}
            >
              
              <Paper elevation={4} sx={{ padding: "1rem" }}>
                <Typography variant="h6" sx={{ textAlign: "center" }}>
                  Polonex
                </Typography>
                <FormControl>
                  <form autoComplete="off" onSubmit={handleSubmit}>
                    <Box
                      sx={{
                        display: "flex",
                        alignItems: "flex-end",
                        marginBottom: "1rem",
                      }}
                    >
                      <AccountCircle
                        sx={{ color: "action.active", mr: 1, my: 0.5 }}
                      />
                      <TextField
                        required
                        label="username"
                        variant="standard"
                        onChange={handleUsername}
                        error={usernameError}
                      />
                    </Box>

                    <Box
                      sx={{
                        display: "flex",
                        alignItems: "flex-end",
                        marginBottom: "1rem",
                      }}
                    >
                      <KeyIcon
                        sx={{ color: "action.active", mr: 1, my: 0.5 }}
                      />
                      <TextField
                        required
                        label="password"
                        variant="standard"
                        type="password"
                        onChange={handlePassword}
                        error={passwordError}
                      />
                    </Box>
                    <Button
                      sx={{ width: "100%" }}
                      type="submit"
                      variant="contained"
                      disabled={loading}
                    >
                      Submit
                    </Button>
                  </form>
                  <Button variant="text" onClick={toggleRegister}>
                    Create account
                  </Button>
                </FormControl>
              </Paper>
            </Box>
          )}
        </div>
      )}
    </ThemeProvider>
  );
}
